.background {
    position: absolute;
    z-index: -2;
    top: 0; left: 0;

    
    width: 100%; 
    height: 110vh;

    @include mq(small) {
        height: 100%;
    }
    
    @include mq(medium) {
        clip-path: polygon(0 0, 100% 0, 45% 100%, 0 100%);
        width: 1620px;
        height: 1165px;
        max-width: 98%;
    }
    background: hsl(210, 100%, 15%);
    

    & > div {
        z-index: -2;
        height: 100vh;
        //background: #111;
        background: $dark-blue;
        @include mq(medium) {
            display: none;
        }
    }
}